<template>
    <div class="app-beer-crate">

        <div
            :class="{'expanded-crate': isCrateExpanded, 'fixed': isFixed}"
            id="beer-crate-sidebar">
            <div
                v-if="!isCrateExpanded"
                @click="isCrateExpanded = true"
                class="expand-crate-button">+</div>
            <div
                v-if="isCrateExpanded"
                @click="isCrateExpanded = false"
                class="expand-crate-button"><i class="icon-cancel"/></div>

            <h3 class="title">Crate</h3>

            <ul class="crate-tab-bar">
                <li
                    :class="{'selected': activeIndex === 0}"
                    @click="activeCrateIndex=0">One</li>
                <li
                    :class="{'selected': activeIndex === 1}"
                    @click="activeCrateIndex=1">Two</li>
                <li
                    :class="{'selected': activeIndex === 2}"
                    @click="activeCrateIndex=2">Three</li>
            </ul>

            <div
                class="clearfix"
                style="margin-bottom: 20px;"/>

            <section
                id="beer-crate"
                class="beer-crate"
                @drop="drop($event)"
                @dragover="allowDrop($event)">
                <div
                    v-for="(item, beerIndex) of activeCrate"
                    class="beer-item"
                    :key="beerIndex">
                    <img
                        draggable="true"
                        @drag="drag(beerIndex)"
                        :src="item"
                        alt="Beer" >
                </div>
            </section>

            <section
                class="beer-trash"
                @drop="dropTrash($event)"
                @dragover="allowDrop($event)">
                Drop empty bottles here please.
            </section>

            <p class="quote-text">
                Drag beers to the crate to have them appear in the crate!
            </p>

            <p class="quote-author">
                <i class="icon-quote-left"/> capJavert
            </p>
        </div>

    </div>
</template>

<script>
import ConditionsUtil from '../helpers/conditions.util'
import EventBus from '../helpers/event-bus'
import state from '../models/state'

export default {
    name: 'BeerCrate',
    data() {
        return {
            crates: [],
            selectedBeerIndex: null,
            onCratesChanged: null,
            isCrateExpanded: false,
            isFixed: false,
            activeIndex: state.session.activeCrateIndex
        }
    },
    created() {
        this.crates = this.crateService.crates

        this.crateService.changeEvent.$on('crates-changed', () => {
            this.crates = this.crateService.crates
        })

        EventBus.$on('sidebar-fixed', isFixed => {
            this.isFixed = isFixed
        })
    },
    computed: {
        activeCrate: {
            get() {
                if (ConditionsUtil.isNull(this.crates[this.activeCrateIndex])) {
                    return []
                }

                return this.crates[this.activeCrateIndex]
            }
        },
        activeCrateIndex: {
            get() {
                return this.activeIndex
            },
            set(value) {
                this.activeIndex = value
                state.session.activeCrateIndex = value
            }
        }
    },
    methods: {
        allowDrop(event) {
            event.preventDefault()
        },
        drop(event) {
            event.preventDefault()
            const beerImageSrc = event.dataTransfer.getData('text')

            if (this.activeCrate.length < 20 && ConditionsUtil.isNotNullNorEmpty(beerImageSrc)) {
                this.activeCrate.push(beerImageSrc)
                this.crateService.crates = this.crates
            }
        },
        dropTrash(event) {
            event.preventDefault()

            if (ConditionsUtil.isNotNull(this.selectedBeerIndex)) {
                this.activeCrate.splice(this.selectedBeerIndex, 1)
                this.selectedBeerIndex = null

                this.crateService.crates = this.crates
            }
        },
        drag(index) {
            this.selectedBeerIndex = index
        }
    }
}
</script>

