<template>
    <header>
        <div class="app-toolbar">
            <router-link to="/home">
                <div class="logo">
                    <img
                        class="symbol"
                        alt="Beerup"
                        src="../assets/images/symbol.png" >
                    <img src="../assets/images/logo.png" >
                </div>
            </router-link>

            <div class="nav-button-container">
                <button
                    @click="toggleMenu()"
                    class="nav-button"><i class="icon-menu"/></button>
            </div>

            <nav class="navigation main-nav">
                <ul>
                    <li><router-link to="/home">Home</router-link></li>
                    <li><router-link to="/favorites">Favorites</router-link></li>
                    <li
                        v-if="!isAuth"
                        @click="login()">
                        <a class="login"><i class="icon-google"/>&nbsp;Sign in</a>
                    </li>
                    <li
                        v-if="isAuth"
                        title="Your favorites and beer crates are synced with your Google account.">
                        <a class="sync">
                            <i class="icon-google"/>&nbsp;Firebase sync ON
                    </a></li>
                    <li><router-link
                        class="button"
                        to="/join">Join</router-link></li>
                </ul>
            </nav>
        </div>
    </header>
</template>

<script>
import * as firebase from 'firebase'

export default {
    name: 'Header',
    data() {
        return {
            loginProvider: new firebase.auth.GoogleAuthProvider(),
            isAuth: !!firebase.auth().currentUser
        }
    },
    created() {
        this.loginProvider.addScope('https://www.googleapis.com/auth/plus.login')
    },
    methods: {
        login() {
            firebase
                .auth()
                .signInWithRedirect(this.loginProvider)
                .then()
        },
        toggleMenu() {
            const button = document.querySelector('.nav-button')
            const navigation = document.querySelector('.main-nav')

            if (navigation.className.includes('toggle-on')) {
                button.innerHTML = '<i class="icon-menu"></i>'
                navigation.className = 'navigation main-nav'
            } else {
                button.innerHTML = '<i class="icon-cancel"></i>'

                navigation.animate([{ opacity: 0 }, { opacity: 1 }], 400)
                navigation.className = 'navigation main-nav toggle-on'
            }
        }
    }
}
</script>

<style scoped lang="scss">
