    ul {
        float: right;
    }
}

.nav-button {
    float: right;
    background: none;
    font-family: Montserrat, sans-serif;
    font-size: 18px;
    border: none;
    padding: 0;
    margin-top: 10px;
    margin-right: 20px;
    outline: none;
    color: #ffc80a;
}

.nav-button-container {
    display: none;
}

@media (max-width: 768px) {
    nav.main-nav {
        display: none;
        opacity: 0;
        grid-area: navigation;

        &.toggle-on {
            display: block;
            opacity: 1;
        }
    }

    .app-toolbar {
        grid-template-columns: 60% 40%;
        grid-template-areas: '. .' 'navigation navigation';
    }
