import * as firebase from 'firebase'
import StorageService from './storage.service'
import ConditionsUtil from '../helpers/conditions.util'

class FavoriteService extends StorageService {
    favoritesCache = []

    get storageKey() {
        return 'beerup-favorites'
    }

    /**
     * Load current favorites into memory from local storage
     *
     * @return {number[]}
     */
    get favorites() {
        if (this.isStorageAvailable) {
            return JSON.parse(localStorage.getItem(this.storageKey))
        }
        return []
    }

    /**
     * Set current favorites to local storage
     * @param {number[]} value
     */
    set favorites(value) {
        this.favoritesCache = value

        if (this.isStorageAvailable) {
            localStorage.setItem(this.storageKey, JSON.stringify(value))
            this.changeEvent.$emit('favorites-changed', true)

            if (firebase.auth().currentUser) {
                const userId = firebase.auth().currentUser.uid
                firebase
                    .database()
                    .ref(`users/${userId}/${this.storageKey}`)
                    .set(value)
            }
        }
    }

    constructor() {
        super()

        if (this.isStorageAvailable) {
            if (ConditionsUtil.isNull(this.favorites)) {
                // if no favorites stored init storage
                localStorage.setItem(this.storageKey, JSON.stringify([]))
            } else {
                this.favoritesCache = this.favorites
            }
        }
    }

    favorite(beer) {
        this.favoritesCache.push(beer)

        this.favorites = this.favoritesCache
    }

    unfavorite(beer) {
        const model = this.favoritesCache.filter(obj => beer.id === obj.id)
        const index = this.favoritesCache.indexOf(model[0])

        if (index > -1) {
            this.favoritesCache.splice(index, 1)
            this.favorites = this.favoritesCache
        }
    }

    isFavorite(beer) {
        return ConditionsUtil.isNotNullNorEmpty(
            this.favoritesCache.filter(obj => beer.id === obj.id)
        )
    }
}

const favoriteService = new FavoriteService()
export default favoriteService
