import * as Rx from 'rxjs'
import ConditionsUtil from '../helpers/conditions.util'
import { Validator, emailRegex } from '../models/validator'

class JoinServiceMock {
    /**
     * Subscribe to Beerup service method mock
     *
     * @param {JoinModel} model
     * @returns {Observable<Validator>}
     */
    joinBeerUp(model) {
        const validator = new Validator()
        validator.errors = this.validate(model)

        // adds random 500 - 2000 ms delay
        return Rx.Observable.of(validator).delay(Math.floor(Math.random() * 2000 + 500))
    }

    validate(model) {
        const errors = []

        if (ConditionsUtil.isNullOrEmpty(model.name)) {
            errors.push('name')
        }

        if (!emailRegex.test(model.email)) {
            errors.push('email')
        }

        if (ConditionsUtil.isNullOrEmpty(model.phone)) {
            errors.push('phone')
        }

        if (ConditionsUtil.isNullOrEmpty(model.rsvp)) {
            errors.push('rsvp')
        }

        return errors
    }
}

const joinService = new JoinServiceMock()
export default joinService
