/**
 * Helper class for scrolling to specific field element
 */
class ScrollHelper {
    classToScrollTo = null

    constructor(classToScrollTo) {
        this.classToScrollTo = classToScrollTo
    }

    doScroll() {
        if (!this.classToScrollTo) {
            return
        }
        try {
            const elements = document.getElementsByClassName(this.classToScrollTo)

            if (elements.length === 0) {
                return
            }

            elements[0].scrollIntoView()
        } finally {
            this.classToScrollTo = null
        }
    }
}

export default ScrollHelper
