<template>
    <div class="app-beer-list">
        <div
            class="content-container"
            id="beer-list-container">
            <ul
                v-if="beers.length > 0"
                class="beer-list">
                <li
                    v-for="beer of beers"
                    class="beer-card"
                    :key="beer.id"
                    @click="showDetails(beer)">
                    <i
                        class="icon-heart"
                        :class="{'favourite': favoriteService.isFavorite(beer) }"
                        @click="toggleFavorite(beer, $event)"
                    />

                    <div class="beer-card-container">
                        <div class="frame">
                            <img
                                @dragstart="drag($event)"
                                draggable="true"
                                :src="beer.image_url"
                                :alt="beer.name" >
                        </div>

                        <aside class="beer-info">
                            <h4>{{ beer.name }}</h4>
                            <p class="detail">IBU<span>{{ beer.ibu }}</span></p>
                            <p class="detail">ABV<span>{{ beer.abv }}%</span></p>
                        </aside>
                    </div>
                </li>
            </ul>

            <ul
                class="beer-list beer-list-loader"
                v-if="isLoading">
                <li
                    v-for="(_, index) in Array(16).fill(true)"
                    class="beer-card"
                    :key="index">
                    <i class="icon-heart"/>

                    <div class="beer-card-container">
                        <div class="frame"/>

                        <aside class="beer-info">
                            <h4>BEER TITLE</h4>
                            <p class="detail">IBU</p>
                            <p class="detail">ABV</p>
                        </aside>
                    </div>
                </li>
            </ul>

            <div
                v-if="!isLoading && beers.length > 0"
                class="loader-container">
                <div
                    @click="loadMore()"
                    v-if="showLoadMore"
                    class="load-more">Load more beers</div>
            </div>

            <BeerDetail
                v-if="selectedBeer"
                :beer="selectedBeer"
                @close-detail="closeDetails()"
            />
        </div>
    </div>
</template>

<script>
import BeerDetail from './BeerDetail'

export default {
    name: 'BeerList',
    components: { BeerDetail },
    props: {
        beers: {
            type: Array,
            required: true
        },
        isLoading: {
            type: Boolean,
            default: false
        },
        showLoadMore: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            selectedBeer: null
        }
    },
    methods: {
        toggleFavorite(beer, event) {
            event.stopPropagation()

            if (!this.favoriteService.isFavorite(beer)) {
                this.favoriteService.favorite(beer)
            } else {
                this.favoriteService.unfavorite(beer)
            }
        },
        showDetails(beer) {
            this.selectedBeer = beer
        },
        closeDetails() {
            this.selectedBeer = null
        },
        drag(event) {
            event.dataTransfer.setData('text', event.target.src)
        },
        loadMore() {
            this.$emit('load-more')
        }
    }
}
</script>

