import Vue from 'vue'
import VueRx from 'vue-rx'
import Rx from 'rxjs/Rx'
import * as firebase from 'firebase'
import App from './App.vue'
import router from './router'
import crateService from './services/crate.service'
import favoriteService from './services/favorite.service'
import firebaseConfig from './firebase.config'
import ConditionsUtil from './helpers/conditions.util'
import './assets/css/styles.css'
import EventBus from './helpers/event-bus'
import setup from './setup'

// setup service worker, web push and other PWA stuff
setup()

Vue.config.productionTip = false

Vue.use(VueRx, Rx)

// global per view state
Vue.mixin({
    data() {
        return {
            crateService,
            favoriteService,
            fixedOffset: null,
            fixedMobileOffset: null,
            isSidebarFixed: false,
            dirty: {}
        }
    },
    methods: {
        toggleFixedBeerCrate() {
            const beerCrateSidebar = document.getElementById('beer-crate-sidebar')

            if (beerCrateSidebar === null) {
                return
            }

            if (beerCrateSidebar.className !== 'fixed') {
                this.fixedOffset = beerCrateSidebar.offsetTop
            }

            if (this.isSidebarFixed !== window.pageYOffset >= this.fixedOffset + 100) {
                this.isSidebarFixed = !this.isSidebarFixed
                EventBus.$emit('sidebar-fixed', this.isSidebarFixed)
            }
        },
        toggleFixedBeerCrateMobile() {
            const beerCrateSidebar = document.getElementById('beer-crate-sidebar')
            const beerList = document.getElementById('beer-list-container')

            if (beerList === null) {
                return
            }

            if (beerCrateSidebar.className !== 'fixed') {
                this.fixedMobileOffset = beerList.offsetTop
            }

            if (this.isSidebarFixed !== window.pageYOffset >= this.fixedMobileOffset) {
                this.isSidebarFixed = !this.isSidebarFixed
                EventBus.$emit('sidebar-fixed', this.isSidebarFixed)
            }
        }
    }
})

// firebase init
firebase.initializeApp(firebaseConfig)

// catch google oauth redirect
firebase
    .auth()
    .getRedirectResult()
    .then(() => {
        if (firebase.auth().currentUser) {
            const userId = firebase.auth().currentUser.uid

            firebase
                .database()
                .ref(`/users/${userId}/${favoriteService.storageKey}`)
                .once('value')
                .then(snapshot => {
                    const favorites = snapshot.val()

                    if (ConditionsUtil.isNotNull(favorites)) {
                        favoriteService.favorites = favorites
                    }
                })

            firebase
                .database()
                .ref(`/users/${userId}/${crateService.storageKey}`)
                .once('value')
                .then(snapshot => {
                    const crates = snapshot.val()

                    if (ConditionsUtil.isNotNull(crates)) {
                        crateService.crates = crates
                    }
                })
        }

        new Vue({
            router,
            render: h => h(App)
        }).$mount('#app')
    })
