<template>
    <div
        v-if="!!beer"
        id="beer-detail"
        class="modal"
        @click="close()">
        <div class="modal-content">
            <div class="container">
                <i
                    @click="toggleFavorite($event)"
                    class="icon-heart"
                    :class="{'favourite': favoriteService.isFavorite(beer) }"/>
                <i
                    @click="close()"
                    class="icon-cancel"/>

                <section class="beer-card">
                    <div class="beer-card-container">
                        <div class="frame">
                            <img
                                :src="beer.image_url"
                                :alt="beer.name" >
                        </div>

                        <aside class="beer-info">
                            <h4>{{ beer.name }}</h4>

                            <section class="consumption-info">
                                <p class="detail">IBU <span>{{ beer.ibu }}</span></p>
                                <p class="detail">ABV <span>{{ beer.abv }}%</span></p>
                            </section>

                            <div
                                class="clearfix"
                                style="margin-bottom: 20px;"/>

                            <h5><i class="icon-quote-left"/>&nbsp;Description</h5>
                            <p>{{ beer.description }}</p>

                            <h5><i class="icon-chat"/>&nbsp;Tips and tricks</h5>
                            <p>{{ beer.brewers_tips }}</p>

                            <h5><i class="icon-food"/>&nbsp;Food pairing</h5>
                            <p>{{ beer.food_pairing }}</p>
                            <p>Contributed by: <strong>{{ beer.contributed_by }}</strong></p>

                            <button
                                @click="addToCrate($event)"
                                class="join-button">Add to Crate</button>
                        </aside>
                    </div>
                </section>
            </div>
        </div>
    </div>

</template>

<script>
import Beer from '../models/beer'
import state from '../models/state'

export default {
    name: 'BeerDetail',
    props: {
        beer: {
            type: Beer,
            required: true
        }
    },
    methods: {
        toggleFavorite(event) {
            event.stopPropagation()

            if (!this.favoriteService.isFavorite(this.beer)) {
                this.favoriteService.favorite(this.beer)
            } else {
                this.favoriteService.unfavorite(this.beer)
            }
        },
        addToCrate(event) {
            event.stopPropagation()

            this.crateService.addBeerToCrate(this.beer.image_url, state.session.activeCrateIndex)
        },
        close() {
            this.$emit('close-detail')
        }
    }
}
</script>

