<template>
    <div class="main-content">
        <div class="jumbo-container">
            <section class="jumbo-showcase">
                <div class="join-title">
                    <h2>Favorites</h2>
                </div>

                <img src="../assets/images/illustration.png" >
            </section>
        </div>

        <div class="content-container">
            <div class="title-container">
                <h3 class="title">My favorite beers</h3>

                <aside class="sort-container">
                    <ul class="tab-bar">
                        <li class="disabled">Sort</li>
                        <li
                            :class="{'selected': sortColumn === 'name'}"
                            @click="sort('name')">Name</li>
                        <li
                            :class="{'selected': sortColumn === 'ibu'}"
                            @click="sort('ibu')">IBU</li>
                        <li
                            :class="{'selected': sortColumn === 'abv'}"
                            @click="sort('abv')">ABV</li>
                    </ul>
                </aside>
            </div>

            <BeerList
                v-if="beers.length > 0"
                :is-loading="false"
                :show-load-more="false"
                :beers="beers"/>

            <h4 v-if="beers.length === 0">
                Click on the <i class="icon-heart"/> icon inside any beer card on the
                <router-link to="/home">home page</router-link> to add beers to your favorites.
            </h4>

            <BeerCrate class="beer-crate-sidebar"/>
        </div>
    </div>
</template>

<script>
import BeerList from '../components/BeerList'
import BeerCrate from '../components/BeerCrate'

export default {
    name: 'Favorites',
    components: { BeerCrate, BeerList },
    data() {
        return {
            beers: [],
            sortColumn: null
        }
    },
    created() {
        this.beers = this.favoriteService.favorites

        this.favoriteService.changeEvent.$on('favorites-changed', () => {
            this.beers = this.favoriteService.favorites
        })
    },
    methods: {
        sort(column) {
            this.sortColumn = column

            this.beers = this.beers.sort(
                (lft, rft) => (lft[this.sortColumn] > rft[this.sortColumn] ? 1 : -1)
            )
        }
    }
}
</script>

