import registerServiceWorker from './registerServiceWorker'

function handleNotificationPermission(permission) {
    if (permission === 'granted') {
        registerServiceWorker()
    }
}

function setup() {
    if (window.Notification) {
        if (Notification.permission === 'default') {
            const request = Notification.requestPermission(handleNotificationPermission)

            if (request instanceof Promise) {
                request.then(permission => handleNotificationPermission(permission))
            }
        } else if (Notification.permission === 'granted') {
            registerServiceWorker()
        }

        window.addEventListener('online', () => {
            if (Notification.permission === 'granted') {
                return new Notification('Online', {
                    body: 'Connected.',
                    tag: `online-${new Date().getTime()}`
                })
            }

            return null
        })

        window.addEventListener('offline', () => {
            if (Notification.permission === 'granted') {
                return new Notification('Offline', {
                    body: 'No connectivity',
                    tag: `offline-${new Date().getTime()}`
                })
            }

            return null
        })
    }
}

export default setup
