<template>
    <div class="main-content">
        <div class="jumbo-container">
            <section class="jumbo-showcase">
                <div class="join-title">
                    <h4>THE BEERSTER PRO 2.0</h4>
                    <h2>Join&nbsp;our<br >famous<br >beerup!</h2>

                    <router-link to="/join">
                        <button class="join-button">Join</button>
                    </router-link>
                </div>

                <img src="../assets/images/illustration.png" >
            </section>

            <router-link to="/join">
                <button class="join-button border-button">Join</button>
            </router-link>
        </div>

        <div class="content-container">
            <div class="title-container">
                <h3 class="title">Beer</h3>

                <aside class="sort-container">
                    <ul class="tab-bar">
                        <li class="disabled">Sort</li>
                        <li
                            :class="{'selected': sortColumn === 'name'}"
                            @click="sort('name')">Name</li>
                        <li
                            :class="{'selected': sortColumn === 'ibu'}"
                            @click="sort('ibu')">IBU</li>
                        <li
                            :class="{'selected': sortColumn === 'abv'}"
                            @click="sort('abv')">ABV</li>
                    </ul>
                </aside>
            </div>

            <BeerList
                @load-more="loadMore()"
                :is-loading="isLoading"
                :show-load-more="true"
                :beers="beers"/>
            <BeerCrate class="beer-crate-sidebar"/>
        </div>
    </div>
</template>

<script>
import BeerList from '../components/BeerList'
import BeerCrate from '../components/BeerCrate'
import beerService from '../services/beer.service'

export default {
    name: 'Home',
    components: { BeerCrate, BeerList },
    data() {
        return {
            isLoading: false,
            beers: [],
            beerService,
            currentPage: 1,
            sortColumn: null
        }
    },
    created() {
        this.isLoading = true

        this.beerService
            .list(this.beerRequestParams)
            .then(response => {
                this.beers = response.data
                this.isLoading = false
            })
            .catch(e => {
                console.log(e)
                this.isLoading = false
            })
    },
    computed: {
        beerRequestParams() {
            return {
                per_page: 16,
                page: this.currentPage
            }
        }
    },
    methods: {
        loadMore() {
            this.currentPage = this.currentPage + 1
            this.isLoading = true

            this.beerService
                .list(this.beerRequestParams)
                .then(response => {
                    this.beers = this.beers.concat(response.data)
                    this.isLoading = false
                })
                .catch(e => {
                    console.log(e)
                    this.isLoading = false
                })
        },
        sort(column) {
            this.sortColumn = column

            this.beers = this.beers.sort(
                (lft, rft) => (lft[this.sortColumn] > rft[this.sortColumn] ? 1 : -1)
            )
        }
    }
}
</script>

