<template>
    <div class="main-content">
        <div class="jumbo-container">
            <section class="jumbo-showcase">
                <div class="join-title">
                    <h2>Join up</h2>
                </div>

                <img
                    src="../assets/images/illustration.png"
                    alt="Beer crate illustration"
                >
            </section>
        </div>

        <div class="content-container">
            <h3 class="title">Quick, join up before we drink all the beer!</h3>

            <div
                v-if="joinSuccess"
                class="form">
                <h3>Subscription successful!</h3>
                <h4>Check your email for further information and see you at our next Beerup!</h4>
            </div>

            <div
                v-if="!joinSuccess"
                class="form">
                <h4>Personal information</h4>
                <div
                    class="field"
                    :class="{'invalid': errorIsShown('name')}">
                    <input
                        required
                        @keyup="fieldChanged('name')"
                        v-model="joinModel.name"
                        autocomplete="name"
                        id="name"
                        name="name"
                        type="text"
                        placeholder="Elliot Alderson" >
                    <label for="name">Full name*</label>
                </div>
                <span class="validation-message">Full name can't be empty</span>
                <hr >

                <h4>Contact information</h4>
                <div
                    class="field"
                    :class="{'invalid': errorIsShown('email')}">
                    <input
                        required
                        @keyup="fieldChanged('email')"
                        v-model="joinModel.email"
                        autocomplete="email"
                        id="email"
                        name="email"
                        type="email"
                        placeholder="elliot@ecorp.org" >
                    <label for="email">Email*</label>
                </div>
                <span class="validation-message">Email is not valid email</span>

                <div
                    class="field"
                    :class="{'invalid': errorIsShown('phone')}">
                    <input
                        required
                        @keyup="fieldChanged('phone')"
                        v-model="joinModel.phone"
                        autocomplete="tel-national"
                        id="phone"
                        name="tel"
                        type="tel"
                        placeholder="+385 099 1234 567" >
                    <label for="phone">Phone number*</label>
                </div>
                <span class="validation-message">Phone is not valid phone number</span>
                <hr >

                <h4
                    :class="{'invalid': errorIsShown('rsvp')}">RSVP*
                </h4>
                <section class="field-group">
                    <label class="radio-container">I'm coming!
                        <input
                            required
                            @change="fieldChanged('rsvp')"
                            v-model="joinModel.rsvp"
                            name="rsvp"
                            type="radio"
                            title="I'm coming!"
                            value="1" >
                        <span class="indicator"/>
                    </label>

                    <label class="radio-container">Maybe?
                        <input
                            required
                            @change="fieldChanged('rsvp')"
                            v-model="joinModel.rsvp"
                            name="rsvp"
                            type="radio"
                            title="Maybe?"
                            value="2" >
                        <span class="indicator"/>
                    </label>

                    <label class="radio-container">Can't make it
                        <input
                            required
                            @change="fieldChanged('rsvp')"
                            v-model="joinModel.rsvp"
                            name="rsvp"
                            type="radio"
                            title="Can't make it"
                            value="3">
                        <span class="indicator"/>
                    </label>
                </section>

                <textarea
                    v-model="joinModel.comment"
                    placeholder="Something you'd like to add?"/>
                <hr >

                <section class="field-group">
                    <label class="checkbox-container">Let me know about future beerups!
                        <input
                            v-model="joinModel.subscribe"
                            title="Subscribe"
                            name="subscribe"
                            type="checkbox" >
                        <span class="indicator"/>
                    </label>
                    <label class="checkbox-container">Remind me one day before beerup!
                        <input
                            v-model="joinModel.reminder"
                            title="Reminder"
                            name="reminder"
                            type="checkbox" >
                        <span class="indicator"/>
                    </label>
                </section>

                <button
                    @click="submit()"
                    type="submit"
                    v-if="!isLoading"
                    class="join-button">Join up</button>
                <button
                    type="button"
                    v-if="isLoading"
                    class="join-button">...</button>
            </div>
        </div>
    </div>

</template>

<script>
import { JoinModel, Validator } from '../models/validator'
import joinService from '../services/join.service.mock'
import ScrollHelper from '../helpers/scroll.util'

export default {
    name: 'Join',
    data() {
        return {
            joinSuccess: false,
            isLoading: false,
            joinModel: new JoinModel(),
            joinService,
            validator: new Validator(),
            isValidated: false
        }
    },
    created() {
        this.joinModel.comment = ''
        this.joinModel.subscribe = true
        this.joinModel.reminder = false
    },
    methods: {
        submit() {
            this.validate()

            this.isLoading = true

            this.joinService.joinBeerUp(this.joinModel).subscribe(
                response => {
                    this.validator = response

                    if (this.validator.isSuccess) {
                        this.joinSuccess = true
                    } else {
                        new ScrollHelper('invalid').doScroll()
                    }
                },
                error => this.handleError(error),
                () => {
                    this.isLoading = false
                }
            )
        },
        validate(passive) {
            if (passive !== true) {
                this.isValidated = true
            }

            this.validator.errors = this.joinService.validate(this.joinModel)
        },
        errorIsShown(fieldName) {
            return (
                this.validator.errors.indexOf(fieldName) !== -1 &&
                (Object.prototype.hasOwnProperty.call(this.dirty, fieldName) || this.isValidated)
            )
        },
        fieldChanged(fieldName) {
            this.dirty[fieldName] = true

            this.validate(true)
        }
    }
}
</script>

